body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen and (max-width: 600px) {
  .container {
    grid-template-areas: "header header header header" "side side side side" "main main main main" "footer footer footer footer";
    grid-template-columns: 1fr 1fr 1fr 1fr; }
    .container .all-heroes {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-auto-rows: minmax(18vw, 1fr); }
    .container .selected-heroes {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: repeat(3, minmax(8vw, 1fr)); }
    .container .active-list {
      padding: 0;
      display: flex;
      flex-flow: row wrap; }
    .container .active-synergies,
    .container .all-synergies {
      padding: 0;
      display: flex;
      flex-flow: row wrap;
      align-content: center; }
      .container .active-synergies > *,
      .container .all-synergies > * {
        flex: 1 1 100%; }
        .container .active-synergies > *:first-child,
        .container .all-synergies > *:first-child {
          flex: 1 1 100%; } }

@media only screen and (min-width: 600px) {
  .container {
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-areas: "header  header header" "main main side" "footer footer footer";
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 60px 1fr 60px; }
  .all-heroes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(19, minmax(5vw, 1fr)); }
  .selected-heroes {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(2, minmax(8vw, 1fr)); }
  .active-list {
    display: flex;
    flex-flow: row wrap; }
  .active-synergies,
  .all-synergies {
    display: flex;
    flex-flow: row wrap;
    align-content: center; }
    .active-synergies > *,
    .all-synergies > * {
      flex: 0 1 45%; }
      .active-synergies > *:first-child,
      .all-synergies > *:first-child {
        flex: 1 1 100%; } }

.selected {
  -webkit-filter: grayscale(100%) brightness(40%) sepia(100%) hue-rotate(-50deg) saturate(600%) contrast(0.8);
          filter: grayscale(100%) brightness(40%) sepia(100%) hue-rotate(-50deg) saturate(600%) contrast(0.8); }

.disabled {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%); }

.hero {
  height: 100%;
  width: 100%;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat; }
  .hero:hover {
    opacity: .3; }

.header, .footer {
  background: #1e2a38;
  color: white; }
  .header strong, .footer strong {
    margin: 0;
    display: block; }

.container {
  background: #4D5B6C; }

.active-filters {
  text-transform: capitalize; }
  .active-filters > div {
    margin: 5px;
    padding: 5px;
    background: #C9B89C; }
    .active-filters > div:hover {
      box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.9) inset;
      background: #4A6D63; }

.active-list, .all-synergies {
  background: #4D5B6C;
  color: white; }

.all-heroes {
  background: #081320; }

.selected-heroes {
  background: #081320;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.9) inset; }

.active-synergies {
  background: white;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.9) inset; }

.active-list, .all-synergies, .active-synergies {
  text-transform: capitalize; }
  .active-list > div, .all-synergies > div, .active-synergies > div {
    padding: 5px;
    box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.75); }
  .active-list .selected, .all-synergies .selected, .active-synergies .selected {
    color: #1e2a38;
    font-weight: 500; }

.active-list, .active-synergies, .all-synergies {
  padding: 10px; }
  .active-list > *, .active-synergies > *, .all-synergies > * {
    margin: 10px; }

.container {
  display: grid; }

.container > div {
  padding: 10px; }

.header {
  grid-area: header; }

.main {
  grid-area: main; }

.side {
  grid-area: side; }

.footer {
  grid-area: footer; }

.header {
  display: flex;
  align-items: center; }

.active-filters {
  display: flex;
  flex-flow: row wrap;
  padding-bottom: 10px; }
  .active-filters > strong {
    flex: 1 1 100%;
    color: white; }
  .active-filters > div {
    flex-grow: 0;
    flex-shrink: 1;
    flex-basis: calc(100%/3 - 20px); }

